console.log('');

import { makeMatrix, makeRow, shuffle, generator } from './toolkit';
import { renderMatrixDom } from './ui';

import $ from 'jquery';

const matrix = generator( state => {
  if(state.done){
    $('#describe').html(`${state.payload}`);
  }else{
    $('#describe').html(`${state.payload}`);
  }
});

console.log(matrix);

renderMatrixDom(matrix, $('#container'));



